
import { JDGetAssestByFInterID, JDSubmitSsDormitory, upLoadImg } from '@/api/getData'
import { mapState } from 'vuex'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import $ from 'jquery'
export default {
data() {
return {
result: '',
fileList: [],
upLoadImgList: [],
FEmpNumber: this.$route.query.FEmpNumber,
FSpouseEmpNumber: this.$route.query.FSpouseEmpNumber,
FItemID:this.$route.query.FItemID,
FInterID:this.$route.query.FItemID
};
},
components: {
XButton
},
mounted() {
},
methods: {
addPic() {
$('input[type=file]').trigger('click')
return false
},
async imgChange(e) {
let imgBroadcastListBase64 = []
if (!e.target.files.length) return ;
let array = Object.keys(e.target.files).map(key=> e.target.files[key]) 
if (array.length > 5) {
array = array.filter((item, index) => {
if (index < 5) {
return item
}
})
}
let fileList = array.map(async (item, index) => {
let res = await this.uploadImgToBase64(item)
return res.result
})
for (const textPromise of fileList) {
imgBroadcastListBase64.push(await textPromise)
}
if (this.fileList.length + imgBroadcastListBase64.length > 5) { // 如果 超过五张则提示超过五张 不得上传
this.$vux.alert.show({
title: '注意',
content: '最多可上传五张图片',
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
console.log('Plugin: I\'m hiding now')
}
})
return
}
this.fileList = [...this.fileList, ...imgBroadcastListBase64] 






},
async upLoadImg() {
let form = {
FCheckInterID: this.result.FCheckInterID,
EmpCode: this.userInfo1[0].Fnumber,
Pictures: []
}
form.Pictures = this.fileList.map((item) => {
return {photo: item.replace(/.*;base64,/, '')}
})
form.Pictures = JSON.stringify(form.Pictures)
console.log(form)
let res = await upLoadImg(form)
console.log(res)
this.$vux.toast.text(res)
},
async submitSsDormitory(){
let Json = {
FItemID: this.FItemID,
FEmpID: this.FEmpID,
FEmpName: this.FEmpName,
FSpouseEmpID:this.FSpouseEmpID,
Fdate:this.Fdate,
FHobby:this.FHobby,
FNote:this.FNote
}
console.info(Json)
let data = await JDSubmitSsDormitory({
FEmpCode: FEmpCode,
Json:Json
})

}
}
};



